import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';

class App extends React.Component {

    constructor(props) {
       super(props);

       this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }

    state = {
        theposition: 0,
        name: 'The Sculpting of Earth',
        snowAnimation: false,
        listItems: require('./resources/earth.json'),
        loading: false,
        width: 0,
        height: 0,
        topic: 'earth',
    };

    

    componentDidMount() {
        // scroll.scrollTo(100);
        window.addEventListener('scroll', this.listenToScroll);
        this.updateWindowDimensions();
    }
      
    componentWillUnmount() {
        window.removeEventListener('scroll', this.listenToScroll);
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    startScroll() {
        scroll.scrollToBottom({
            duration: 45000,
            delay: 100,
            smooth: "linear",
        });
    }

    stopScroll() {
       //  scroll.scrollTo(this.state.theposition);
    }
      
    listenToScroll = () => {
        const winScroll =
          document.body.scrollTop || document.documentElement.scrollTop
      
        const height =
          document.documentElement.scrollHeight -
          document.documentElement.clientHeight
      
        const scrolled = winScroll / height

        if (scrolled < 0.15) {
            this.setState({
                theposition: scrolled,
                name: 'The Sculpting of Earth',
                snowAnimation: false
            });
        } else if (scrolled > 0.15 && scrolled < 0.30) {
            this.setState({
                theposition: scrolled,
                name: 'Earth Cooling and Primitive Life',
                snowAnimation: false
            });
        } else if (scrolled > 0.30 && scrolled < 0.45) {
            this.setState({
                theposition: scrolled,
                name: 'An Oxygenated Atmosphere',
                snowAnimation: true,
            });
        } else if (scrolled > 0.45 && scrolled < 0.60) {
            this.setState({
                theposition: scrolled,
                name: 'Cambrian Explosion and Fossil Records',
                snowAnimation: false
            });
        } else if (scrolled > 0.60 && scrolled < 0.75) {
            this.setState({
                theposition: scrolled,
                name: 'The Age of Reptiles and Dinosaurs',
                snowAnimation: false
            });
        } else if (scrolled > 0.75) {
            this.setState({
                theposition: scrolled,
                name: 'The Age of Mammals and Homo Sapiens',
                snowAnimation: false
            });
            // 
        }
    }

    render() {
        const { width, height } = this.state;
        if (this.state.loading) {
            return (
                <div style={{ display: 'flex', width, height, alignItems: 'center', justifyContent: 'center', backgroundColor: 'black' }}>
                    <div style={{ width: 64, height: 64, alignItems: 'center', justifyContent: 'center' }}>
                        <ReactLoading type="bubbles" color="#fff" height={64} width={64} />
                    </div> 
                </div>
            );
        }
        return (
            <Link activeClass="active"
                to="target"
                spy={true}
                smooth={true}
                hashSpy={true}
                offset={50}
                duration={500}
                delay={1000}
                isDynamic={true}
                onSetActive={this.handleSetActive}
                onSetInactive={this.handleSetInactive}
                ignoreCancelEvents={false}
            >
             
            {this.state.snowAnimation ? <SnowStorm animationInterval={50} usePositionFixed={true} flakesMax={64} flakesMaxActive={48} /> : null}
            <div style={{ flex: 1, backgroundColor: 'BLACK' }}>
                <Menu fixed='top' inverted>
                <Container>
                    <Menu.Item as='a' header onClick={() => { 
                        this.setState({ loading: true, topic: 'earth' });
                        setTimeout(() => {
                            this.setState({ loading: false })}, 1000);
                        }
                    }>
                        Earth
                    </Menu.Item>
                    <Menu.Item as='a' header onClick={() => { 
                        this.setState({ loading: true, topic: 'fetus' });
                        setTimeout(() => {
                            this.setState({ loading: false })}, 1000);
                        }
                    }>
                        Fetus
                    </Menu.Item>
                    {/* <Menu.Item as='a'>Home</Menu.Item>

                    <Dropdown item simple text='Dropdown'>
                    <Dropdown.Menu>
                        <Dropdown.Item>List Item</Dropdown.Item>
                        <Dropdown.Item>List Item</Dropdown.Item>
                        <Dropdown.Divider />
                        <Dropdown.Header>Header Item</Dropdown.Header>
                        <Dropdown.Item>
                        <i className='dropdown icon' />
                        <span className='text'>Submenu</span>
                        <Dropdown.Menu>
                            <Dropdown.Item>List Item</Dropdown.Item>
                            <Dropdown.Item>List Item</Dropdown.Item>
                        </Dropdown.Menu>
                        </Dropdown.Item>
                        <Dropdown.Item>List Item</Dropdown.Item>
                    </Dropdown.Menu>
                    </Dropdown> */}
                </Container>
                </Menu>
                    <div style={{ marginBottom: 50 }}>
                    <VerticalTimeline>
                        {this.state.listItems["earth"].map((item) => (
                            <VerticalTimelineElement
                                style={{ marginTop: 20 }}
                                className="vertical-timeline-element--education"
                                contentStyle={{ background: 'rgb(0, 0, 0)', color: '#fff' }}
                                iconStyle={{ background: 'rgb(0,0,0)', color: '#fff' }}
                                date={item.date}
                                icon={giicons[item.icon]}
                            >
                                <h3 className="vertical-timeline-element-title">{item.header}</h3>
                                <h4 className="vertical-timeline-element-subtitle">{item.title}</h4>
                                <p>{item.description}</p>
                            </VerticalTimelineElement>
                        ))}
                        <VerticalTimelineElement
                            className="vertical-timeline-element--education"
                            contentStyle={{ background: 'rgb(0, 0, 0)', color: '#fff' }}
                            iconStyle={{ background: 'rgb(16, 204, 82)', color: '#fff' }}
                            date="NOW"
                        >
                            <h3 className="vertical-timeline-element-title">Modern Earth</h3>
                        </VerticalTimelineElement> 
                    </VerticalTimeline>
                    </div>
                
                <Menu style={{ height: 50, backgroundColor: 'black' }} fixed='bottom' inverted>
                    <Container>
                        <Menu.Item as='a' header>
                        {/* <Image size='mini' src='/logo.png' style={{ marginRight: '1.5em' }} /> */}
                        {this.state.name}
                        </Menu.Item>
                        <Menu.Item as='b' style={{ alignSelf: 'center' }}>
                            <GiPlayButton onClick={() => this.startScroll()} />
                            <GiPauseButton style={{ marginLeft: 5 }} onClick={() => this.stopScroll()} />
                        </Menu.Item>
                        {/* <Menu.Item as='a'>Home</Menu.Item>

                        <Dropdown item simple text='Dropdown'>
                        <Dropdown.Menu>
                            <Dropdown.Item>List Item</Dropdown.Item>
                            <Dropdown.Item>List Item</Dropdown.Item>
                            <Dropdown.Divider />
                            <Dropdown.Header>Header Item</Dropdown.Header>
                            <Dropdown.Item>
                            <i className='dropdown icon' />
                            <span className='text'>Submenu</span>
                            <Dropdown.Menu>
                                <Dropdown.Item>List Item</Dropdown.Item>
                                <Dropdown.Item>List Item</Dropdown.Item>
                            </Dropdown.Menu>
                            </Dropdown.Item>
                            <Dropdown.Item>List Item</Dropdown.Item>
                        </Dropdown.Menu>
                        </Dropdown> */}
                    </Container>
                </Menu>
            </div>
            </Link>
        );
    }
}

ReactDOM.render(<App />, document.querySelector('#root'));

